.header {
    display: flex;
    justify-content: space-around;
    height: 150px;
    width: 100%;
    background-color:  orange;
    /* opacity: 0.6; */
    background-attachment: fixed;
    align-items: center;
 }

 .one{
   width: 180px;
   height: 120px;
 }

 .two{
   width: 180px;
   height: 80px;
 }
 .three{
   width: 180px;
   height: 90px;
 }
 

 
 @media(max-width: 425px){
    .Header{
       margin: 0 auto;
       padding: 0 auto;
    }
    .header{
       height: 100px;
    }
    .one{
      width: 80px;
      height: 40px;
    }
    .two{
      width: 80px;
      height: 40px;
    }
    .three{
      width: 80px;
      height: 40px;
    }
 }